import React, { Component } from 'react';
import { Dialog, Button } from 'zzc-design-mobile';
import './index.scss';
import '../../../style/style.scss';

export default class App extends Component {
    constructor( props ) {
        super( props );
        this.state = {
            visible: false,
            visible2: false,
            visible3: false,
            visible4: false,
            visible5: false,
            visible6: false,
            visible7: false
        };
    }

    openDefaultDialog() {
        this.setState( {
            visible: true
        } );
    }

    openDialog() {
        this.setState( {
            visible2: true
        } );
    }
    openNoMaskDialog() {
        this.setState( {
            visible5: true
        } );
    }
    openAnDialog() {
        this.setState( {
            visible3: true,
            visible4: true
        } );
    }
    openCustomAnDialog() {
        this.setState( {
            visible6: true,
            visible7: true
        } );
    }
    
    render() {
        return (
            <div className='zzc-demo'>
                <div className='zzc-demo-header'>
                    <h1 className='zzc-demo-title'>Dialog </h1>
                </div>
                <div className='zzc-demo-body'>
                    <Button onClick={() => { this.openDefaultDialog(); }}>dialog</Button>
                    {this.state.visible && <Dialog
                        visible={this.state.visible}
                        maskClose={true}
                        closeCallback={() => { this.setState({ visible: false }); }}
                    >
                        <div className='modal-text'>...</div>
                    </Dialog>}

                    <Button onClick={() => { this.openDialog(); }}>titlefooterdialog</Button>
                    {this.state.visible2 && <Dialog
                        title={<div>123123123</div>}
                        footer={<div><Button onClick={() => { this.setState( { visible2: false } ); }}></Button></div>}
                        visible={this.state.visible2}
                        closeCallback={() => { this.setState({ visible2: false }); }}
                    >
                        <div className='modal-text'>......</div>
                    </Dialog>}

                    <Button onClick={() => { this.openNoMaskDialog(); }}>dialog</Button>
                    {this.state.visible5 && <Dialog
                        title={<div>123123123</div>}
                        className='black'
                        transparent={true}
                        footer={<div><Button onClick={() => { this.setState( { visible5: false } ); }}></Button></div>}
                        visible={this.state.visible5}
                        closeCallback={() => { this.setState({ visible5: false }); }}
                    >
                        <div className='modal-text'>......</div>
                    </Dialog>}

                    <Button onClick={() => { this.openAnDialog(); }}>dialog</Button>
                    {this.state.visible4 && <Dialog
                        maskTransitionName='zzc-fade'
                        transitionName='zzc-zoom'
                        maskClose={true}
                        title={<div>123123123</div>}
                        footer={<div><Button onClick={() => { this.setState( { visible3: false } ); }}></Button></div>}
                        visible={this.state.visible3}
                        closeCallback={() => { this.setState({ visible4: false }); }}
                    >
                        <div className='modal-text'>......</div>
                    </Dialog>}

                    <Button onClick={() => { this.openCustomAnDialog(); }}>dialog</Button>
                    {this.state.visible6 && <Dialog
                        maskTransitionName='zzc-fade'
                        transitionName='custom-an'
                        className='custom-dialog'
                        maskClose={true}
                        title={<div></div>}
                        footer={<div><Button onClick={() => { this.setState( { visible7: false } ); }}></Button></div>}
                        visible={this.state.visible7}
                        closeCallback={() => { this.setState({ visible6: false }); }}
                    >
                        <div className='modal-text'>......</div>
                    </Dialog>}
                </div>
            </div>
        );
    }
}


